<script>
import { reactive, onMounted, toRefs } from 'vue'

import sHeader from '@/components/SimpleHeader'
import Result from '../Pay/Result'

import { useRouter } from 'vue-router'
export default {
  components: {
      Result,sHeader
  },
  setup() {
    const router = useRouter()
