           },
         {id:4,
           title:'线下权益兑换',
             tag:'权益',
             type:'free',
             time:'2022.06.26',
             price:"68.8"
         }],
        payResult:false,
        handletype:'',
        info:{},
    })

    onMounted(async () => {

    })

    const goBack = () => {
      router.go(-1)
    }

    const goTo = (r, query) => {
      router.push({ path: r, query: query || {} })
    }

      const goToDetail=(item)=>{
         if(item.type=='free'){
             state.handletype='freeSuccess';
             state.info={
                 id:item.id,
                 orderNo:'NFT250944693540',
                 time:item.time
             }
         }else{
             state.handletype='success';
             state.info={
                 id:item.id,
                 orderNo:'NFT250944693540',
                 payType:'支付宝',
                 price:item.price,
                 time:item.time
             }
         }

          state.payResult=true;

      }
      const closeResult=()=>{
          state.payResult=false;
      }
    return {
      ...toRefs(state),
      goBack,
      goTo,
        goToDetail,
        closeResult
    }
  }
}
